@import "../../global.scss";

.svgPlot {
  .shadowPath {
    fill: var(--background-color);
  }

  .dataPath {
    stroke: white;

    &.temperature {
      stroke: orange;
    }

    &.wind {
      stroke: cornflowerblue;
    }

    &.probabilityOfPrecipitation {
      stroke: #204bd4;
    }

    &.rain {
      stroke: #204bd4;
    }
  }

  * {
    stroke: var(--text-color);
  }
}